#root .risks-detail {
  font-family: 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei', 'WenQuanYi Micro Hei',
    sans-serif;
  padding: 20px;
  > p {
    font-size: 14px;
    line-height: 24px;
  }
  .title-big {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
    text-align: center;
  }

  .title-normal {
    font-size: 16px;
    line-height: 24px;
    font-weight: bold;
  }
}
